<template>
  <li>
    <div class="shop-info">
      <input type="checkbox" @change="singleCheckeds(sid,pid)" class="check shop-info-check" v-model="data.checked">
      <div class="shop-info-img">
        <a href="#">
          <img :src="data.pic"/>
        </a>
      </div>
      <div class="shop-info-text">
        <h4>{{data.title}}</h4>
        <p>{{data.color}}</p>
        <div class="shop-price">
          <div class="shop-pices">
            ￥<b class="price">{{data.price}}</b>
          </div>
          <div class="shop-arithmetic">
            <a class="minus" @click.prevent.stop="minus(sid,pid)">-</a>
            <span class="num">{{data.qal}}</span>
            <a class="plus" @click.prevent.stop="add(sid,pid)">+</a>
          </div>
        </div>
      </div>
    </div>
  </li>
</template>

<script>
    export default {
        props:["data","add","minus","sid","pid","singleCheckeds"],
        name: "AmazonCartsShopProduct"
    }
</script>

<style scoped>

</style>



