<template>
<div class="categroytotal">
  <amazon-category-header></amazon-category-header>
  <amazon-category-section :data="categroyPro"></amazon-category-section>
  <amazon-category-footer></amazon-category-footer>
</div>
</template>

<script>
  import categroyDataApis from "../apis/categroyProapis";
    import categorycss from "../assets/css/category.css"
  import iconfont from "../assets/css/iconfont.css"
    import AmazonCategoryHeader from "../components/category/AmazonCategoryHeader";
    import AmazonCategoryFooter from "../components/category/AmazonCategoryFooter";
    import AmazonCategorySection from "../components/category/AmazonCategorySection";
    export default {
        name: "AmazonCategory",
      components: {AmazonCategorySection, AmazonCategoryFooter, AmazonCategoryHeader,categorycss,iconfont},
      data(){
        return{
          categroyPro:[]
        }
      },
      methods:{
        _initgetCategroyData(){
          categroyDataApis.getcategroyProData(data=>{
            this.categroyPro = data
            console.log(this.categroyPro)
          })
        }
      },
      created() {
        this._initgetCategroyData()
      },
      mounted() {

      }

    }

</script>

<style scoped>
.categroytotal{
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
}
</style>



