<template>
  <div class="indextotal">
    <amazon-index-header></amazon-index-header>
    <amazon-index-container :data="indexChinapro"></amazon-index-container>
    <amazon-index-footer></amazon-index-footer>
  </div>
</template>

<script>
  import indexChinaProdataApis from "../apis/indexChinaProapis"
  import jumpPage from "../assets/js/public"
  import  IndexFont from  "../assets/css/iconfont.css";
  import indexcss from "../assets/css/index.css";
  import AmazonIndexHeader from "../components/index/AmazonIndexHeader";
    import AmazonIndexContainer from "../components/index/AmazonIndexContainer";
    import AmazonIndexFooter from "../components/index/AmazonIndexFooter";
  import AmazonIndexSlide from "../components/index/AmazonIndexSlide";
    export default {
        name: "AmazonIndex",
        components:{
          AmazonIndexSlide,
          AmazonIndexHeader,
          AmazonIndexFooter,
          AmazonIndexContainer,
          indexcss,
          IndexFont,
          jumpPage
        },
      data(){
          return{
            indexChinapro:[]
          }
      },
      methods:{
        _initgetIndexChinaData(){
          indexChinaProdataApis.getChinaProData(data=>{
            this.indexChinapro = data
            console.log(this.indexChinapro)
          })
        }
      },
      created() {
        this._initgetIndexChinaData()
      },
      mounted() {

      }
    }
</script>

<style scoped>
  @import "../assets/css/index.css";
.indextotal{
  height: 100%;
  width: 100%;
  flex-direction: column;
  display: flex;
}
</style>



