<template>
  <section class="loginsection">
    <div class="sectionTop">
      <h3>欢迎</h3>
    </div>
    <div class="sectionCon">
      <div class="firstcon">
        <input type="radio" name="reg">
        <h3>创建账户</h3>
        <p>.Amazon的新客户</p>
      </div>
      <div class="firstcon firstspecial">
        <input type="radio" name="reg" checked>
        <h3>登录</h3>
        <p>.已注册客户</p>
      </div>
      <div class="secondcon">
        <input type="text" placeholder="手机号/邮箱" class="amazonphone" v-model="username">
        <input type="password" placeholder="Amazon密码" v-model="userpass">
        <!--<input type="text" placeholder="Amazon密码" style="display: none;" class="amazonpass" v-if="show" v-show="show">-->
      </div>
      <div class="thirdcon">
        <input type="checkbox" class="checkshow">
        <p>显示密码</p>
        <span>忘记密码</span>
      </div>
      <div class="thirdcon">
        <input type="checkbox">
        <p>记住登录状态</p>
      </div>
      <div class="forthcon">
        <input type="button" value="登录" id="loginbtn" class="loginbtn" v-on:click="recheck">
      </div>
      <div class="fifthcon">
        <p>微信注册用户可以使用绑定的手机号和密码登录</p>
        <p>若尚未设置密码，<a href="#">请点击此处设置</a></p>
      </div>
    </div>
    <div class="sectionBot">
      <p>@1999-2018,Amazon.com,inc,or its affiliates</p>
    </div>
  </section>
</template>

<script>
    import login from "../../assets/js/login"
    export default {
      name: "AmazonLoginSection",
      components: {login},
      data() {
        return {
          username: "",
          userpass: ""
        }
      },
      methods: {
        recheck: function (username) {
          // console.log(this.username)
          if (this.username == "18893782758") {
            if (this.userpass = "666") {
              alert("登录成功")
              // this.$router.push({path:'/mypage'})
              // this.$router.push({
              //   path: '/mypage',
              //   name: 'mypage',
              //   params: {
              //     username:username
              //   }
                this.$router.push({name:'mypage',query:{username:'666'}});
              // })
            } else {
              alert("密码或用户名错误，请重新输入")
            }
          }
        }
      }
    }
</script>

<style scoped>
  @import "../../assets/css/login.css";
</style>



