      mounted(){
        console.log(this.$route.query.username)
        this.item = this.$route.query.username
        if(this.item){
          this.isShow=true
          this.isLogin=false
        }
      },
      methods:{
          xx:function(){
            // console.log(this.$route.query.username)
          }
      }
    }
</script>

<style scoped>
  @import "../../assets/css/mypage.css";
</style>



