import Vue from 'vue'
import Router from 'vue-router'
import AmazonIndex from "../pages/AmazonIndex";
import AmazonMypage from "../pages/AmazonMypage"
import AmazonSpecies from  "../pages/AmazonSpecies"
import AmazonCategroy from "../pages/AmazonCategory"
import AmazonDetails from "../pages/AmazonDetails"
import AmazonLogin from "../pages/AmazonLogin"
import AmazonCarts from "../pages/AmazonCarts"
import AmazonShoplist from "../pages/AmazonShoplist"

Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/',
      name:'index',
      component:AmazonIndex
    },
    {
      path:'/mypage',
      name:'mypage',
      component:AmazonMypage
    },
    {
      path:'/species',
      name:'species',
      component:AmazonSpecies
    },
    {
      path:'/categroy',
      name:'categroy',
      component:AmazonCategroy
    },
    {
      path:'/details',
      name:'details',
      component:AmazonDetails
    },
    {
      path:'/login',
      name:'login',
      component:AmazonLogin
    },
    {
      path:'/carts',
      name:'carts',
      component:AmazonCarts
    },
    {
      path:'/shoplist',
      name:'shoplist',
      component:AmazonShoplist
    }
  ]
})



