<template>
  <div class="shop-group-item">
    <div class="shop-name">
      <input type="checkbox" class="check" @change="shopCheck(sid)" v-model="data.checked">
      <h3>{{data.shopname}}</h3>
    </div>
    <ul>
        <amazon-carts-shop-product :singleCheckeds="singleCheckeds" :sid="sid" :pid="pid" :add="add" :minus="minus" :data="product" :key="pid" v-for="(product,pid) in data.products"></amazon-carts-shop-product>
    </ul>
    <div class="shopPrice">
      本店总计：
      ￥<span class="shop-total-amount ShopTotal">{{data|shopCounterFilter}}</span>
    </div>
  </div>
</template>

<script>
    import AmazonCartsShopProduct from "./AmazonCartsShopProduct";
    export default {
      props:["data","sid","pid","add","minus","singleCheckeds"],
        name: "AmazonCartsShop",
      components: {AmazonCartsShopProduct},
      methods:{
        shopCheck(i){
          this.$emit("shopChecked",i)
        }
      },
      filters:{
        shopCounterFilter(shop){
          let total = 0;
          shop.products.forEach((product,index)=>{
            if(product.checked){
              total+=product.price*product.qal
            }
          })
          return total
        }
      }
    }
</script>

<style scoped>

</style>



