<template>
  <div class="index-panel-first-section">
    <div class="swiper-container">
      <div class="swiper-wrapper">
        <div class="swiper-slide"><img src="../../../static/image/lun01.jpeg"></div>
        <div class="swiper-slide"><img src="../../../static/image/lun02.jpeg"></div>
        <div class="swiper-slide"><img src="../../../static/image/lun03.jpeg"></div>
        <div class="swiper-slide"><img src="../../../static/image/lun04.jpeg"></div>
        <div class="swiper-slide"><img src="../../../static/image/lun05.jpeg"></div>
        <div class="swiper-slide"><img src="../../../static/image/lun06.jpeg"></div>
        <div class="swiper-slide"><img src="../../../static/image/lun07.jpeg"></div>
        <div class="swiper-slide"><img src="../../../static/image/lun08.jpeg"></div>
      </div>
      <div class="swiper-pagination"></div>
    </div>
  </div>
</template>

<script>
  import  $ from  "jquery";
  import Swiper from 'swiper';
    export default {
        name: "AmazonIndexSlide",
        mounted() {
          var swiper = new Swiper('.swiper-container', {
            spaceBetween: 30,
            centeredSlides: true,
            loop:true,
            autoplay: {
              delay: 2500,
              disableOnInteraction: false,
            },
            pagination: {
              el: '.swiper-pagination',
              clickable: true,
            }
          });
        }
    }
</script>

<style scoped>
  @import "../../../node_modules/swiper/dist/css/swiper.css";
</style>



