<template>
  <div class="specialtotal">
    <amazon-species-header></amazon-species-header>
    <section class="speciessection">
      <div class="sectop">
        <div class="sectopleft">
          <p>超过6000个结果</p>
        </div>
        <div class="sectopright">
          <img src="../../static/image/line.png" alt="">
          <span>筛选(1)</span>
        </div>
      </div>
      <div class="goodsList">
        <div class="goods-item" :key="index"  v-for="(pro,index) in speciesProdata.products" >
          <div class="goods-item-left">
            <router-link :to="'/details?id='+index"><img :src=pro.propic alt=""></router-link>
          </div>
          <div class="goods-item-right">
            <span class="hotpro">{{pro.hotpro}}</span>
            <h4 class="title">{{pro.title}}</h4>
            <p class="tit fontspec">{{pro.tit}}</p>
            <p class="money">￥<span class="moneypri">{{pro.price}}</span></p>
            <p class="tit">{{pro.cus}}</p>
            <p class="tit">更多选项</p>
          </div>
        </div>
      </div>
    </section>
    <amazon-species-footer></amazon-species-footer>
  </div>
</template>

<script>
    import speciesProdataApis from "../apis/speciesProapis"
    import speciescss  from "../assets/css/species.css"
    import Iconfont from "../assets/css/iconfont.css";
    import AmazonSpeciesHeader from "../components/species/AmazonSpeciesHeader";
    import AmazonSpeciesSection from "../components/species/AmazonSpeciesSection";
    import AmazonSpeciesFooter from "../components/species/AmazonSpeciesFooter";
    export default {
        name: "AmazonSpecies",
        components: {AmazonSpeciesFooter,AmazonSpeciesHeader,Iconfont,speciescss},
      data(){
          return{
            speciesProdata:[]
          }
      },
      methods:{
          _initSpeciesProData(){
            speciesProdataApis.getspeciesProData(data=>{
              this.speciesProdata = data
              console.log(this.speciesProdata)
            })
          }
      },
      created() {
          this._initSpeciesProData()
      }
    }
</script>

<style scoped>
  .specialtotal{
    width:100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background-color: #fff;
  }
</style>



