<template>
  <footer class="payment-bar">
    <div class="all-checkbox">
      <input type="checkbox" class="check goods-check" @change="ShopAllChecked" v-model="data.checked"/>
      全选
    </div>
    <div class="shop-total" v-if="data.shops">
      <span>总价：</span>
      <strong>{{data | totalPriceFilter}}</strong>
    </div>
    <a href="#" class="settlement">结算</a>
  </footer>
</template>

<script>
    export default {
        props:["data"],
        name: "AmazonCartsFooter",
        filters:{
          totalPriceFilter(cartsInfo){
            let total = 0
            cartsInfo.shops.forEach((shop,sid)=>{
              shop.products.forEach((product,pid)=>{
                if(product.checked){
                  total+=product.price * product.qal
                }
              })
            })
            return total
          }
        },
        methods:{
          ShopAllChecked(){
            this.$emit("ShopsChecked")
          }
        }
    }
</script>

<style scoped>

</style>



