<template>
<div class="cartstotal">
  <amazon-carts-header></amazon-carts-header>
  <amazon-carts-shop-list :singleCheckeds="singleCheckedSel" :add="addQal" :minus="minusQal" @shopItemChecked="shopCheckAll" v-if="cartsInfo.shops" :data="cartsInfo.shops"></amazon-carts-shop-list>
  <amazon-carts-footer @ShopsChecked="ShopCartAllChecked" v-if="cartsInfo" :data="cartsInfo"></amazon-carts-footer>
</div>
</template>

<script>
    import cartsDataApis from "../apis/carts"
    import cartscss from "../assets/css/mycarts.css"
   import iconfont from "../assets/css/iconfont.css"
    import AmazonCartsHeader from "../components/carts/AmazonCartsHeader";
    import AmazonCartsShopList from "../components/carts/AmazonCartsShopList";
    import AmazonCartsFooter from "../components/carts/AmazonCartsFooter";
    export default {
        name: "AmazonCarts",
      components: {AmazonCartsShopList, AmazonCartsFooter, AmazonCartsHeader,iconfont,cartscss},
      data(){
          return {
            cartsInfo:[],
            totalPrice:0,
            checked:true
          }
      },
      methods:{
          //初始化数据
          _initgetCartsData(){
            cartsDataApis.getCartsData(data=>{
              this.cartsInfo = data
              console.log(this.cartsInfo)
              this._countCarts(data)
            })
          },
        /**
         * 统计总价格
         */
          _countCarts(cartsInfo){

        },
        /**
         * 根据店铺的下标来确定当前店铺的选中状态
         * @param i
         */
          shopCheckAll(i){
            console.log(i)
            let isSelected = this.cartsInfo.shops[i].checked
            this.cartsInfo.shops[i].products.forEach((product,index)=>{
                product.checked = isSelected
            })
          },

        /**
         * 店铺的全选
         *
         */
        ShopCartAllChecked(){
            let isSelected = this.cartsInfo.checked
            this.cartsInfo.shops.forEach((shop,sid)=>{
                shop.checked = isSelected
                shop.products.forEach((product,pid)=>{
                  product.checked = isSelected
                })
            })
        },


        singleCheckedSel(sid,pid){
          let isSelected = this.cartsInfo.shops[sid].products[pid].checked;
          this.cartsInfo.shops[sid].checked = isSelected;
          this.cartsInfo.checked = isSelected
        },


        /**
         * 根据店铺的编号与商品的编号来确定商品的数量
         * @param sid
         * @param pid
         */
        addQal(sid,pid){
          this.cartsInfo.shops[sid].products[pid].qal++
        },
        /**
         *
         * @param sid
         * @param pid
         */
        minusQal(sid,pid){
          if(this.cartsInfo.shops[sid].products[pid].qal <=1){
            this.cartsInfo.shops[sid].products[pid].qal = 1
          }else{
            this.cartsInfo.shops[sid].products[pid].qal--
          }
        }
      },
      created(){
        this._initgetCartsData()
      },
      mounted() {

      }
    }
</script>

<style scoped>
  .cartstotal{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
  }
</style>



