<template>
  <section class="mycartssection">
    <div class="shopping">
        <amazon-carts-shop :singleCheckeds="singleCheckeds" :add="add" :minus="minus" @shopChecked="shopCheckedAll" :sid="index" :data="shop" :key="index" v-for="(shop,index) in data"></amazon-carts-shop>
    </div>
  </section>
</template>

<script>
    import AmazonCartsShop from "./AmazonCartsShop";
    export default {
        name: "AmazonCartsShopList",
      components: {AmazonCartsShop},
      props:["data","add","minus","singleCheckeds"],
      methods:{
        shopCheckedAll(i){
          this.$emit("shopItemChecked",i)
        }
      }
    }
</script>

<style scoped>

</style>



